.booking__content {
	width: 100%;
	position: absolute;
	bottom: 0;
	text-align: center;
}

.booking__hotel-name {
	text-transform: uppercase;
	margin-bottom: 5px;
}

.booking__hotel-details {
	color: #999;
	font-size: 0.9rem;
	margin-bottom: 20px;
}

.booking__current-price {
	color: #69c982;
	font-size: 1.4rem;
	font-weight: bold;
}

.booking__old-price {
	color: #999;
	text-decoration: line-through;
	margin-bottom: 10px;
}

.booking__buy-btn {
	cursor: pointer;
	width: 100%;
	background: #2f2f2f;
	color: white;
	padding: 15px 0;
	font-size: 1rem;
	font-weight: bold;
	text-transform: uppercase;
	border: 0;
	border-bottom-left-radius: 5px;
	border-bottom-right-radius: 5px;
}
